// Colors
// ---------------------------------

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);

// Project palette

$color-sapphire: #102654;
$color-egyptian-blue: #103aae;
$color-resolution-blue: #1c3374;
$color-crimson: #d10a42;
$color-ghost-white: #f8f9fc;
$color-torch-red: #ed0233;
$color-black-russian: #030a1a;
$color-torch-red-error: #ff121f;
$color-spindle: #b7becd;
$color-persian-blue: #2e34ad;
$color-light-slate-grey: #74819c;
$color-link-water: #c7c8cc;
$color-dark-cerulean: rgba(20, 54, 122, 0.5);
$color-grey: rgba(120, 120, 120, 0.2);
$color-whisper: rgba(232, 232, 232, 0.48);

// Gradient

$crimson-to-cobalt: linear-gradient(128deg, #d10a42 0%, #0041ca 45.58%);
$torch-red-to-cobalt: linear-gradient(97deg, #ed0233 17.6%, #003eb7 95.31%);
$resolution-blue-to-venetian-red: linear-gradient(90deg, #1c3477 0%, #1c3477 0.01%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
$crimson-to-smalt: linear-gradient(to bottom right, #d10a42, #00359f);

// Typography
// ---------------------------------

$fw-default: 300;
$fz-default: 16px;
$lh-default: 24px;
$ff-default: "Roboto Condensed", "Arial", sans-serif;
$ff-heading: "Alumni Sans", "Arial", sans-serif;

// Animation
// ---------------------------------

$trans-default: 0.3s ease;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-768: 768px;
$vp-1366: 1366px;
